import {
	ProcessingResults,
	ProcessStepElement,
	ProcessStepElementBase,
} from '@jumoo/processing';
import { customElement, html } from '@umbraco-cms/backoffice/external/lit';
import { uSyncRestore } from '@jumoo/usync-expansions';
import { RestorePointProcessingOptions } from '@jumoo/usync-expansions';

@customElement(uSyncRestore.steps.restoreCreateComplete)
export class uSyncRestoreCreateCompleteElement
	extends ProcessStepElementBase
	implements ProcessStepElement
{
	constructor() {
		super();
	}

	render() {
		const results = this.results as ProcessingResults;
		const options = this.options as RestorePointProcessingOptions;

		return html`<uui-box>
			<p>
				Restore point created:
				<strong>${options.title}</strong>
			</p>

			<umb-localize key="uSyncRestore_createComplete"></umb-localize>

			<p>Contains ${results.results['changes'] ?? 'unknown'} items</p>
		</uui-box>`;
	}
}

export default uSyncRestoreCreateCompleteElement;
